import React, { useEffect, useState } from 'react'
import PropTypes from 'prop-types'
import { useForm, Controller } from 'react-hook-form'

import { DatePicker } from '@mui/x-date-pickers'
import { FormControl, InputLabel, MenuItem, Select } from '@mui/material'
import dayjs from 'dayjs'
import { calculateRate, NIGHTS, PEOPLE, ROOM_TYPES } from './Constants'

const CalculatedSection = ({ calculatedRate, occupancy, duration }) => (
  <div className='wrapper'>
    <p>
      Average nightly rate (per person): <span className='emphasis'>${Number(calculatedRate / duration).toFixed(2)}</span>.
    </p>
    <p>
      Over the course of {duration} nights, that sums up to <span className='emphasis'>${calculatedRate}</span>.
    </p>
    <p>
      The total for the entire room is <span className='emphasis'>${Number(calculatedRate * occupancy).toFixed(2)}</span>.
    </p>
    <p>
      Please note that this includes <span className='emphasis'>food</span>, <span className='emphasis'>drink</span>, and{' '}
      <span className='emphasis'>transportation</span> to/from the airport.
    </p>
    <p>
      If you would like to stay additional nights, please contact our travel agent Heather Dzubinski (email at the top of the
      &quot;Travel&quot; section of the wedding website). Parents traveling with children under 18 should also contact her for assistance
      with alternative accommodations.
    </p>
  </div>
)
CalculatedSection.propTypes = {
  calculatedRate: PropTypes.number,
  occupancy: PropTypes.number,
  duration: PropTypes.number
}

const RateForm = () => {
  const [calculatedRate, setCalculatedRate] = useState(0)
  const {
    handleSubmit,
    watch,
    getValues,
    control,
    formState: { isSubmitSuccessful, errors }
  } = useForm({ mode: 'all' })

  let onSubmit = (data) => {
    setCalculatedRate(calculateRate(data))
  }

  const validateDuration = (value) => {
    const { startDate } = getValues()
    const day = new Date(startDate).getUTCDate()
    if (Number(value) + day > 13) return `Please select ${13 - day} nights or fewer, or choose an earlier check-in date.`
    return null
  }

  const validateStartDate = (value) => {
    const day = new Date(value).getUTCDate()
    const month = new Date(value).getUTCMonth()
    const year = new Date(value).getUTCFullYear()
    if (year !== 2023 || month !== 10 || day < 8 || day > 10) return 'Please select a date between 11/8/2023 and 11/10/2023.'
    return null
  }

  useEffect(() => {
    handleSubmit(onSubmit)()
  }, [])

  useEffect(() => {
    const subscription = watch(handleSubmit(onSubmit))
    return () => subscription.unsubscribe()
  }, [handleSubmit, watch])

  return (
    <>
      <div className='wrapper no-border'>
        <form onSubmit={handleSubmit(onSubmit)}>
          <Controller
            control={control}
            name='startDate'
            defaultValue={dayjs('2023-11-09')}
            rules={{ validate: validateStartDate }}
            render={({ field: { onChange, onBlur, value } }) => (
              <div>
                <DatePicker
                  onChange={onChange}
                  onBlur={onBlur}
                  value={value}
                  label='Check-in date'
                  slotProps={{
                    textField: {
                      className: 'white-outline'
                    }
                  }}
                  sx={{
                    svg: { color: 'white' },
                    input: { color: 'white' },
                    label: { color: 'white' }
                  }}
                  minDate={dayjs('2023-11-08')}
                  maxDate={dayjs('2023-11-10')}
                />
                {errors?.startDate && <div className='error'>{errors?.startDate?.message}</div>}
              </div>
            )}
          />
          <Controller
            control={control}
            name='duration'
            rules={{ validate: validateDuration }}
            render={({ field: { onChange, onBlur, value }, fieldState: { error } }) => (
              <FormControl error={error}>
                <InputLabel focused={true} id='duration'>
                  Nights
                </InputLabel>
                <Select
                  labelId='duration'
                  id='duration'
                  value={value}
                  label='Nights'
                  onChange={onChange}
                  onBlur={onBlur}
                  className='white-outline'
                  sx={{
                    color: 'white',
                    svg: { color: 'white' }
                  }}
                >
                  {NIGHTS.map((val) => (
                    <MenuItem key={val} value={val}>
                      {val}
                    </MenuItem>
                  ))}
                </Select>
                {errors?.duration && <div className='error'>{errors?.duration?.message}</div>}
              </FormControl>
            )}
          />
          <Controller
            control={control}
            name='occupancy'
            render={({ field: { onChange, onBlur, value } }) => (
              <FormControl>
                <InputLabel focused={true} id='occupancy'>
                  Number of people occupying the room
                </InputLabel>
                <Select
                  labelId='occupancy'
                  id='occupancy'
                  value={value}
                  label='Number of people occupying the room'
                  onChange={onChange}
                  onBlur={onBlur}
                  className='white-outline'
                  sx={{
                    color: 'white',
                    svg: { color: 'white' }
                  }}
                >
                  {PEOPLE.map((val) => (
                    <MenuItem key={val} value={val}>
                      {val}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            )}
          />
          <Controller
            control={control}
            name='roomType'
            render={({ field: { onChange, onBlur, value }, fieldState: { error } }) => (
              <FormControl error={error}>
                <InputLabel focused={true} id='roomType'>
                  Room type
                </InputLabel>
                <Select
                  labelId='roomType'
                  id='roomType'
                  value={value}
                  label='Room type'
                  onChange={onChange}
                  onBlur={onBlur}
                  className='white-outline'
                  sx={{
                    color: 'white',
                    svg: { color: 'white' }
                  }}
                >
                  {Object.entries(ROOM_TYPES).map(([key, value]) => (
                    <MenuItem key={key} value={key}>
                      {value}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            )}
          />
        </form>
      </div>
      {isSubmitSuccessful && (
        <CalculatedSection calculatedRate={calculatedRate} occupancy={Number(getValues()?.occupancy)} duration={getValues()?.duration} />
      )}
    </>
  )
}

export default RateForm
